import { ApolloError } from 'apollo-client';
import { PasswordValidator } from './../../validators/passwordValidator';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Apollo } from 'apollo-angular';
import { SIGNUP } from 'src/app/graphQL/queries/mutations/signup';
import { Router } from '@angular/router';
import { SAVE_USER } from 'src/app/graphQL/queries/mutations/local/save-user';

@Component({
  selector: 'app-signup-page',
  templateUrl: './signup-page.component.html',
  styleUrls: ['./signup-page.component.scss']
})
export class SignupPageComponent implements OnInit {
  signUpForm: FormGroup;
  hasError = {
    email: false
  };
  errorMessage: string = '';
  constructor(private apollo: Apollo, private router: Router) {
    this.signUpForm = new FormGroup(
      {
        firstName: new FormControl('', [Validators.required]),
        lastName: new FormControl('', [Validators.required]),
        username: new FormControl('', [Validators.required]),
        email: new FormControl('', [Validators.required]),
        password: new FormControl('', [Validators.required, Validators.minLength(8)]),
        confirmPassword: new FormControl('', [Validators.required, Validators.minLength(8)])
      },
      { validators: [PasswordValidator.MatchPassword] }
    );
  }

  ngOnInit() {}

  private saveUser(user) {
    this.apollo.mutate({ mutation: SAVE_USER, variables: { user } }).subscribe({
      next: data => {
        console.log(data);
      },
      error: e => {
        console.log(e);
      }
    });
  }
  onSubmit() {
    console.log(this.signUpForm.value);

    this.apollo.mutate({ mutation: SIGNUP, variables: { user: this.signUpForm.value } }).subscribe({
      next: ({ data }: any) => {
        const { signup } = data;
        console.log(signup);
        this.saveUser(signup);

        this.router.navigate(['/i/signin']);
      },
      error: (error: ApolloError) => {
        const { email } = error.graphQLErrors[0].extensions.exception;
        this.hasError.email = email;
        // this.hasError.password = password;
        this.errorMessage = error.graphQLErrors[0].message;
      }
    });
  }
}
