import { AbstractControl } from '@angular/forms';

export class PasswordValidator {
  static MatchPassword(ac: AbstractControl) {
    const passworControl = ac.get('password');
    const confirmPasswordControl = ac.get('confirmPassword');
    const password: string = passworControl.value;
    const confirmPassword: string = confirmPasswordControl.value;

    if (password !== confirmPassword) {
      //   passworControl.setErrors({ matchPassword: true });
      confirmPasswordControl.setErrors({ matchPassword: true });
    } else {
      return null;
    }
  }
}
